@keyframes spinning {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes op {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.Ball {
  animation-name: spinning;
  border-radius: 80%;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in, ease-out;
  background-image: radial-gradient(#fff700, #ff0000, #afffb6);
}

.BallRunning {
  animation-name: spinning, op;
  border-radius: 80%;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  background-image: radial-gradient(#fff700, #ff0000, #afffb6);
}

.Ball:hover {
  cursor: pointer;
  animation-name: none;
  transform: scale(1.8);
}

.ball2 {
  margin-top: 1px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-image: radial-gradient(#ff8765, #ffffff, #97fdff);
  height: 63.5vh;
  padding-top: 13%;
}
.ball2 .intro {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.ball2 button {
  color: black;
  width: 6vw;
  height: 4vh;
  background-image: linear-gradient(white, grey);
  border-style: 10px outset;
  border-radius: 15px;
  border-width: 5px;
  border-color: gray;
  font-size: 1.5vh;
  box-shadow: 5px 5px 5px 0px rgb(114, 114, 114);
}
.ball2 button:hover {
  font-weight: bold;
  box-shadow: 0 0 15px 15px rgb(0, 0, 0) outset;
  transform: scale(0.9);
  background-image: linear-gradient(rgb(192, 192, 192), rgb(0, 217, 255));
}
.ball2 button:active {
  transform: scale(1.3);
}

.board {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin-top: 3%;
  width: 90%;
  height: 60%;
  font-family: Webdings;
  font-size: 2vw;
  margin-bottom: 5%;
}

.Air {
  background-color: #afffb6;
}

.y {
  color: red;
  background-color: #fff347;
}

.Wall {
  background-color: #ce9980;
}

.time {
  display: flex;
  text-align: center;
  align-items: flex-start;
  margin-bottom: 13%;
}